import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
import {
	mapState
} from "vuex"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox
	},
	computed: {
		...mapState([
			'userMsgs'
		])
	},
	data() {
		return {
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			bannerIndex: 0,
			topMenu: [{
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, {
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, {
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 0
			}, {
				icon: "kefu",
				text: "客服小蜜",
				size: 26,
				badge: 0
			}, {
				icon: "feedback",
				text: "我要反馈",
				size: 23,
				badge: 0
			}, {
				icon: "share",
				text: "分享",
				size: 26,
				badge: 0
			}],
			menuShow: false,
			value: 1,
			collected: false,
			goodsMsgs: '', //商品详情信息
			bottomImgs: [], //底部图片
		}
	},
	onLoad: function(options) {
		this.$http.lookGoodsDetailMsgs({
			gid: options.goodsid
		}).then(res => {
			this.goodsMsgs = res.data
		})
		this.$http.lookGoodsIsSaved({
			uid:this.userMsgs.id
		}).then(res => {
			var that = this
			if(res.data != 0) {
				var list = res.data
				list.forEach(function(item) {
					if(item.goodsid == options.goodsid) {
						that.collected = true
					}
				})
			}
		})
		// 查询详情页底部多图展示
		this.$http.lookGoodDetailArrayImgs({
			gid: options.goodsid
		}).then(res => {
			console.log("detailArrayImgs", res);
			if(res.data != 0) {
				this.bottomImgs = JSON.parse(res.data.bookdetailimgs)
			}

		})
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();








		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
	},
	methods: {
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.banner[index],
				urls: this.banner
			})
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		// 点击收藏
		collecting: function(e) {
			uni.showLoading({
				title: '操作中',
				mask: true
			});
			this.collected = !this.collected
			var day2 = new Date()
			day2.setTime(day2.getTime());
			var nowday = day2.getFullYear()+"-" + (day2.getMonth()+1) + "-" + day2.getDate();
			var status = 0  //没有收藏状态
			if (this.collected == true) {
				status = 1
			}else {
				status = 0
			}
			this.$http.controlSaveGoods({
				uid:this.userMsgs.id,
				gid:e,
				time:nowday,
				status:status
			}).then(res => {
				uni.hideLoading()
				if(res.data == 1) {
					if(status == 1) {
						uni.showToast({
							title: '收藏课程成功',
							icon:'none',
							duration:3000
						});
					}else {
						uni.showToast({
							title: '成功取消收藏',
							icon:'none',
							duration:3000
						});
					}
				}else {
					this.collected = !this.collected
					uni.showToast({
						title: '服务器异常',
						icon:'none',
						duration:3000
					});
				}
				console.log("收藏Status", res);
			})
		},
		// 查看商品评价
		common: function(e) {
			uni.navigateTo({
				url: '../uni-comment/uni-comment?gid='+e
			})
		},
		// 加入购物车
		showPopup(e) {
			// console.log('加入购物车id=', e.currentTarget.id);
			// console.log("userMsgs", this.userMsgs);
			this.$http.insertIntoCart({
				bookid: e,
				userid: this.userMsgs.id
			}).then(res => {
				if (res.data == 0) {
					uni.showToast({
						title: '商品加入购物车失败',
						icon: 'none',
						duration: 3000
					});
				} else if (res.data == 1) {
					uni.showToast({
						title: '成功加入购物车',
						icon: 'none',
						duration: 3000
					});
				}
			})
		},
		// 立即购买
		nowBuy() {
			uni.showModal({
				title: '无法进行购买',
				content: '因为没有对接支付接口',
			});
		},
		// 联系客服
		connectKF() {
			uni.makePhoneCall({
				phoneNumber:'15215084164'
			})
		},
		// 进入购物车
		gotoCart() {
			uni.switchTab({
				url: '../Cart/Cart',
			});
		}
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
